@import url(https://fonts.googleapis.com/css2?family=Archivo:ital,wght@0,400;0,500;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,600;1,400&display=swap);
.image {
  width: 150px;
  border-radius: 50%;
  margin-bottom: 1.5rem;
  align-self: center;
}

.description-about {
  margin-top: 1rem;
  margin-bottom: 1rem;
  text-align: justify;
  text-indent: 2.5rem;
  line-height: 1.5rem;
  font: 400 1rem 'Open Sans',
    sans-serif;
}

@media (max-width: 768px) {
  .description-about {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    text-align: justify;
    text-indent: 0rem;
  }
}

.image-list>div>img {
  height: 90px;
  padding: 10%;
}

/* SLIDER */
@-webkit-keyframes scroll {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(calc(-250px * 7));
  }
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(calc(-250px * 7));
  }
}



.slider {
  background: var(--color-secondary-light);
  height: 90px;
  margin: 3rem 0;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.slider::before,
.slider::after {
  background: linear-gradient(to right,
      rgb(14 27 42) 0%,
      rgb(255 255 255 / 0%) 100%);
  content: "";
  height: 100px;
  position: absolute;
  width: 200px;
  z-index: 2;
}

.slider::after {
  right: 0;
  top: 0;
  transform: rotateZ(180deg);
}

.slider::before {
  left: 0;
  top: 0;
}

.slider .slide-track {
  -webkit-animation: scroll 40s linear infinite;
          animation: scroll 40s linear infinite;
  display: flex;
  width: calc(250px * 14);
}

.slider .slide {
  height: 100px;
  width: 250px;
}
footer {
  text-align: center;
}
.footer-description {
  font-size: 1rem;
  color: var(--color-secondary);
}

.card-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  padding: 0;
  margin: 0;
}

.container-cards {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 70px;
}

.title-cards {
  padding: 20px 0;
}

@media (max-width: 768px) {
  .container-portifolio {
    margin: 4rem 1rem !important;
  }
}
.card {
  background-color: var(--color-primary);
  border-radius: 8px;
  box-shadow: 2px 2px 5px black;
  width: 25.0rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 20px;
  line-height: 20px;
}

.card-header {
  background-color: var(--color-primary-light);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 10px 20px;
  text-align: left;
  font: 600 1.2rem 'Open Sans',
    sans-serif;
}

.card-body {
  text-align: left;
  padding: 10px 20px;
  min-height: 150px;
  font: 400 1rem 'Open Sans',
    sans-serif;
  font-style: italic;
  line-height: 1.5rem;
  overflow: hidden;

}

.card-footer {
  display: flex;
  justify-content: end;
  padding: 10px 20px;
}

.card-btn {
  width: 120px;
  height: 40px;
  background-color: var(--color-bg);
  border-radius: 8px;
  transition: 550ms;
  font: 600 1rem 'Open Sans',
    sans-serif;
}

.card-btn:hover {
  background-color: var(--color-primary-light);
  transition: 250ms;
}
.container-contatos {
  margin: 5rem 1rem !important;
  padding-bottom: 3rem;
}

.btn-list {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 150px;
  padding-bottom: 50px;
}
.btn {
  margin-left: 1rem;
  margin-right: 1rem;
  width: 120px;
  height: 50px;
  background-color: var(--color-primary);
  border-radius: 8px;
  transition: 250ms;
  display: flex;
  justify-content: space-evenly;
}

.btn:hover {
  background-color: var(--color-primary-light);
  transition: 250ms;
}

@media (max-width: 768px) {
  .btn-list {
    padding-top: 10px;
    padding-bottom: 5px;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .btn {
    flex-wrap: wrap;
    flex-direction: column;
    margin: 10px auto;
    background-color: var(--color-primary);
    border-radius: 8px;
    transition: 1s cubic-bezier(0.215, 0.61, 0.355, 1);
    font-size: 1rem;
    width: 40vw;
    padding: 10px;
    height: 80px;
    justify-content: space-around;
  }
  .btn:active {
    background-color: var(--color-primary-light);
    box-shadow: 2px 2px 5px black;
  }
}

.menu {
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  background-color: var(--color-primary);
  list-style: none;
  height: 3rem;
  box-shadow: 0px 0px 5px black;
}

a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  color: var(--color-secondary-light);
  font-weight: 500;
  font-size: 0.8rem;
}

.menu-item {
  background-color: var(--color-primary);
  flex: 1 1;
  height: 100%;
  cursor: pointer;
  transition: 1s;
  transform-style: flat;
  opacity: 0.8;
}

.menu-item:hover {
  background-color: var(--color-primary-light);
  opacity: 1;
  transition: 1s;
  flex: 2 1;
}

.border-left {
  border-bottom-left-radius: 1rem;
}

.border-right {
  border-bottom-right-radius: 1rem;
}

@media (max-width: 768px) {
  .border-left {
    border-bottom-left-radius: 1rem;
  }

  .border-right {
    border-bottom-right-radius: 1rem;
  }
}
:root {
  --color-bg: #0d1b2a;
  --color-primary: #1b263b;
  --color-primary-light: #415a77;
  --color-secondary: #e5e5e5;
  --color-secondary-light: #ffffff;
}

body {
  background: #0d1b2a;
  background: var(--color-bg);
  background-image: url(/social/static/media/bg.41738a59.png);
  background-size: contain;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: Archivo;
  color: #ffffff;
  color: var(--color-secondary-light);
}

.menu-link {
  font: 600 1rem 'Open Sans',
    sans-serif;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  margin-left: 10%;
  margin-right: 10%;
}

.title {
  font: 600 1.5rem 'Open Sans',
    sans-serif;
  color: #ffffff;
  color: var(--color-secondary-light);
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .container {
    margin-top: 12%;
  }

  .title {
    margin-top: 20px;
    margin-bottom: 20px;
    font: 500 1.2rem Ubuntu, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    color: #ffffff;
    color: var(--color-secondary-light);
    text-transform: uppercase;
    text-align: center;
  }
}
